import React, { useCallback, useEffect, useState } from "react";
import { auth, db } from "../firebase";
import styled, { css } from "styled-components";
import { useLocation } from "react-router-dom";
import { BiLike } from "react-icons/bi";
// import MainHeader from "./MainHeader";

const ContentBox = styled.div`
  max-width: 1000px;
  margin: 0 auto;
  margin-top: 50px;
`;
const Info = styled.div`
  display: flex;
  margin-bottom: 10px;

  & .profileImg {
    width: 100px;
    border-radius: 50%;
  }
`;
const Title = styled.div`
  margin-left: 20px;

  & input {
    border: 1px solid black;
    border-radius: 25px;
    padding: 0 10px;
  }
  & .title {
    font-weight: bold;
    font-size: 25px;
    margin-bottom: 10px;
  }
  & .category {
    font-size: 20px;
  }
`;
const ImgBox = styled.div`
  border: 1px solid black;
  border-radius: 10px;
  height: 300px;
  margin-bottom: 10px;

  ${(props) =>
    props.image &&
    css`
      background-image: url(${props.image});
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
    `}
`;
const TextInput = styled.textarea`
  max-width: 450px;
  min-width: 450px;
  max-height: 300px;
  min-height: 300px;
  width: 450px;
`;
const BtnBox = styled.div`
  display: flex;

  & .like-btn {
    width: 50px;
    height: 20px;
    border: 1px solid black;
    border-radius: 10px;
    margin-right: 10px;
    display: flex;
    align-items: center;
    padding: 0 5px;
    cursor: pointer;
  }
  ${(props) =>
    props.check &&
    css`
      & .like-btn {
        background: #5292f3d0;
        color: white;
      }
    `}
`;

function LookPost() {
  const location = useLocation();
  const { type, postId, likeNum } = location.state;
  console.log(type, postId, likeNum);

  const [posts, setPosts] = useState(null);
  const fetchData = useCallback(() => {
    let postData;
    db()
      .collection("post")
      .doc(postId)
      .get()
      .then((docs) => {
        const data = docs.data();
        postData = { post: data, id: docs.id };
        setPosts(postData);
      });
  }, [postId]);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  const [user, setUser] = useState(null);
  const [likeCheck, setLikeCheck] = useState(false);
  useEffect(() => {
    setUser(auth().currentUser);
  }, []);
  useEffect(() => {
    if (user != null) {
      db()
        .collection("like")
        .doc(user.uid)
        .get()
        .then((doc) => {
          try {
            setLikeCheck(doc.data()[postId].like);
          } catch {
            db()
              .collection("like")
              .doc(user.uid)
              .set({
                [postId]: { like: false },
              });
          }
        });
    }
  }, [postId, user]);

  const [likeCnt, setLikeCnt] = useState(likeNum);

  const onLikeClick = () => {
    if (user === null) {
      alert("로그인 후 이용할 수 있습니다.");
    } else {
      setLikeCheck(!likeCheck);
      if (likeCheck) {
        setLikeCnt((prevNum) => prevNum - 1);
        db()
          .collection("like")
          .doc(user.uid)
          .update({
            [postId]: { like: false },
          });
        db()
          .collection("post")
          .doc(postId)
          .update({
            likeNum: likeCnt - 1,
          });
      } else {
        setLikeCnt((prevNum) => prevNum + 1);
        db()
          .collection("like")
          .doc(user.uid)
          .update({
            [postId]: { like: true },
          });
        db()
          .collection("post")
          .doc(postId)
          .update({
            likeNum: likeCnt + 1,
          });
      }
    }
  };

  return (
    <>
      {/* <MainHeader /> */}
      {posts && (
        <ContentBox>
          <Info>
            <img
              src={posts.post.profileImgUrl}
              alt="ProfileImage"
              className="profileImg"
            />
            <Title>
              <p className="title">{posts.post.title}</p>
              <p className="category">{posts.post.category}</p>
            </Title>
          </Info>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <div style={{ width: "450px" }}>
              <ImgBox image={posts.post.image}></ImgBox>
            </div>
            <TextInput
              name="text"
              placeholder="글 내용"
              value={posts.post.text}
              readOnly
            />
          </div>
          <BtnBox check={likeCheck}>
            <div className="like-btn" onClick={onLikeClick}>
              <BiLike />
              {likeCnt}
            </div>
          </BtnBox>
        </ContentBox>
      )}
    </>
  );
}

export default LookPost;
