import React, { useEffect } from "react";
import { Route, useLocation } from "react-router-dom";
import JoinComponent from "./components/JoinComponent";
import LoginComponent from "./components/LoginComponent";
import MainContent from "./components/MainContent";
import MainHeader from "./components/MainHeader";
import PostComponent from "./components/PostComponent";
import CreatePost from "./components/CreatePost";
import SearchComponent from "./components/SearchComponent";
import MyPageComponent from "./components/MyPageComponent";
import LookPost from "./components/LookPost";

function Home() {
  return (
    <>
      {/* <MainHeader /> */}
      {/* <MainContent /> */}
      <PostComponent />
    </>
  );
}

function App() {
  const location = useLocation();

  return (
    <div>
      {
        location.pathname === "/login" ? null : location.pathname === "/join" ? null : <MainHeader />
      }
      <Route path="/login" component={LoginComponent} />
      <Route path="/join" component={JoinComponent} />
      <Route path="/" exact component={Home} />
      <Route path="/post" component={CreatePost} />
      <Route path="/search" component={SearchComponent} />
      <Route path="/mypage" component={MyPageComponent} />
      <Route path="/postpage" component={LookPost} />
    </div>
  );
}

export default App;
