import React, { useState } from "react";
import styled from "styled-components";
import { FiArrowLeftCircle } from "react-icons/fi";
import { Link, useHistory } from "react-router-dom";
import { signUp, userUpdate } from "../auth";
import { db, auth } from "../firebase";

const Box = styled.div`
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
`;

const BackBtn = styled.div`
  position: absolute;
  display: flex;
  align-items: center;
  border: 1px solid black;
  padding: 5px;
  border-radius: 25px;
  margin-top: 10px;
  font-size: 15px;
  cursor: pointer;
`;

const Img = styled.img`
  width: 200px;
  margin: 0 auto;
  margin: 50px 0 20px;
`;

const InputBox = styled.form`
  position: relative;
  width: 60%;
  margin: 30px auto 0;

  & p {
    margin-top: 10px;
    position: absolute;
    right: 0;
    text-decoration: dashed;
  }
`;

const InputTag = styled.input`
  width: 100%;
  border: 2px black solid;
  border-radius: 20px;
  font-size: 17px;
  padding: 10px 15px;
  margin-bottom: 10px;
  outline: none;
  box-sizing: border-box;
`;

const Button = styled.button`
  margin-top: 10px;
  width: 100%;
  border: 2px black solid;
  border-radius: 20px;
  padding: 10px 15px;
  font-size: 17px;
`;

function JoinComponent() {
  const history = useHistory();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [id, setId] = useState("");

  const handleOnChange = (e) => {
    const type = e.target.name;
    if (type === "email") {
      setEmail(e.target.value);
    } else if (type === "password") {
      setPassword(e.target.value);
    } else if (type === "id") {
      setId(e.target.value);
    }
  };

  const handleOnSubmit = async (e) => {
    e.preventDefault();
    if (email !== "" && password !== "") {
      try {
        await signUp(email, password);
        await userUpdate({
          displayName: id,
          photoURL:
            "https://firebasestorage.googleapis.com/v0/b/landmark-daf63.appspot.com/o/defaultPhoto.png?alt=media&token=b7386266-5255-4bbc-af07-b41d49988d2d",
        })
          .then(() => {
            const user = auth().currentUser;
            if (user !== null) {
              const uid = user.uid;
              db()
                .collection("users")
                .doc(uid)
                .set({
                  posts: [],
                })
                .then(() => {
                  db()
                    .collection("like")
                    .doc(uid)
                    .set({
                      new: null,
                    })
                    .then(() => {
                      history.push("/");
                    });
                });
            }
          })
          .catch((error) => {
            console.log(error);
          });
      } catch (error) {
        console.log(error);
        if (error.code === "auth/email-already-in-use") {
          alert("이 이메일 주소는 이미 다른 계정에서 사용 중입니다.");
        }
      }
    }
  };

  return (
    <Box>
      <BackBtn
        onClick={() => {
          window.history.back();
          return false;
        }}
      >
        <FiArrowLeftCircle />
        &nbsp; 뒤로가기
      </BackBtn>
      <Img src="img/joinlogo.png" alt="LOGO" />
      <hr />
      <InputBox onSubmit={handleOnSubmit}>
        <InputTag
          type="email"
          placeholder="이메일"
          name="email"
          value={email}
          onChange={handleOnChange}
          required
        />
        <InputTag
          type="text"
          placeholder="아이디"
          name="id"
          value={id}
          onChange={handleOnChange}
          pattern="^([A-Za-z0-9_]){6,20}$"
          title="아이디는 최소 6자리에서 최대 20자리까지 입력해야합니다. (영문, 숫자, _)"
          required
        />
        <InputTag
          type="password"
          placeholder="비밀번호"
          name="password"
          value={password}
          onChange={handleOnChange}
          pattern="^(?=.*[A-Za-z])(?=.*\d)(?=.*[@!%#&])[A-Za-z\d@!%#&]{8,16}$"
          title="비밀번호는 최소 8자리에서 최대 16자리까지 숫자, 영문, 특수문자(@!%#&) 각 1개 이상 포함해야합니다."
          required
        />
        <InputTag
          type="password"
          pattern={password}
          title="비밀번호가 일치하지 않습니다."
          placeholder="비밀번호 재입력"
          required
        />
        <Button type="submit">회원가입</Button>
        <Link to="/login">
          <p>로그인</p>
        </Link>
      </InputBox>
    </Box>
  );
}

export default JoinComponent;
