import React, { useCallback, useEffect, useState } from "react";
import styled from "styled-components";
import PostItem from "./PostItem";
import { db } from "../firebase";
import { useLocation } from "react-router-dom";

const PostBox = styled.div`
  max-width: 1000px;
  margin: 0 auto 10px;
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(2, 450px);
  grid-row-gap: 40px;
  justify-content: space-between;
`;

function SearchComponent() {
  const location = useLocation();
  const { type, userInput } = location.state;
  console.log(type, userInput);
  const [posts, setPosts] = useState([]);
  const fetchData = useCallback(() => {
    let postData = [];
    db()
      .collection("post")
      .get()
      .then((docs) => {
        docs.forEach((doc) => {
          const data = doc.data();
          if (type === "search") {
            if (data.title.includes(userInput)) {
              postData.push({ post: data, id: doc.id });
            }
          } else if (type === "category") {
            if (userInput === data.category) {
              postData.push({ post: data, id: doc.id });
            }
          }
        });
        setPosts(postData);
      });
  }, [type, userInput]);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  return (
    <PostBox>
      {posts.map((post) => (
        <PostItem
          key={post.id}
          id={post.id}
          profileImgUrl={post.post.profileImgUrl}
          title={post.post.title}
          category={post.post.category}
          image={post.post.image}
          text={post.post.text}
          likeNum={post.post.likeNum}
          dislikeNum={post.post.dislikeNum}
        />
      ))}
    </PostBox>
  );
}

export default SearchComponent;
