import React, { useEffect, useState } from "react";
import { auth, storage, db } from "../firebase";
import styled, { css } from "styled-components";
// import MainHeader from "./MainHeader";

const ContentBox = styled.div`
  max-width: 1000px;
  margin: 0 auto;
  margin-top: 50px;
`;
const Info = styled.div`
  display: flex;
  margin-bottom: 10px;

  & .profileImg {
    width: 100px;
    border-radius: 50%;
  }
`;
const Title = styled.div`
  margin-left: 20px;

  & input {
    border: 1px solid black;
    border-radius: 25px;
    padding: 0 10px;
  }
  & .title {
    font-weight: bold;
    font-size: 25px;
    margin-bottom: 10px;
  }
  & .category {
    font-size: 20px;
  }
`;
const ImgBox = styled.div`
  border: 1px solid black;
  border-radius: 10px;
  height: 300px;
  margin-bottom: 10px;

  ${(props) =>
    props.image &&
    css`
      background-image: url(${props.image});
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
    `}
`;
const TextInput = styled.textarea`
  max-width: 450px;
  max-height: 300px;
  width: 450px;
`;
const BtnBox = styled.div`
  width: 100%;
  margin-top: 40px;
  display: flex;

  & button {
    margin: 0 auto;
    width: 100px;
    height: 40px;
  }
`;

function CreatePost() {
  const [uid, setUid] = useState("");
  const [profileImg, setProfileImg] = useState("");
  useEffect(() => {
    auth().onAuthStateChanged((user) => {
      if (user) {
        setUid(user.uid);
        setProfileImg(user.photoURL);
      } else {
        window.location.href = "/";
      }
    });
  }, []);

  const [title, setTitle] = useState("");

  const categoryList = ["아시아", "아프리카", "유럽", "아메리카", "오세아니아"];
  const [category, setCategory] = useState(categoryList[0]);

  const [text, setText] = useState("");

  const [file, setFile] = useState(null);
  const [imgBase64, setImgBase64] = useState("");

  const handleChange = (e) => {
    const type = e.target.name;
    if (type === "imgFile") {
      let reader = new FileReader();
      reader.onloadend = () => {
        const base64 = reader.result;
        if (base64) {
          setImgBase64(base64.toString());
        }
      };
      if (e.target.files[0]) {
        reader.readAsDataURL(e.target.files[0]);
        setFile(e.target.files[0]);
      }
    } else if (type === "text") {
      setText(e.target.value);
    } else if (type === "category") {
      setCategory(e.target.value);
    } else if (type === "title") {
      setTitle(e.target.value);
    }
  };

  function handleUpload(e) {
    e.preventDefault();
    const ref = storage.ref(`/images/${file.name}`);
    const uploadTask = ref.put(file);
    uploadTask.on("state_changed", console.log, console.error, () => {
      ref.getDownloadURL().then((url) => {
        e.target.reset();
        setTitle("");
        setCategory(categoryList[0]);
        setText("");
        setImgBase64("");
        setFile(null);

        let doc = db().collection("post").doc();
        doc.set({
          profileImgUrl: profileImg,
          title: title,
          category: category,
          image: url,
          text: text,
          likeNum: 0,
          dislikeNum: 0,
          userId: uid,
        });
        db()
          .collection("users")
          .doc(uid)
          .update({
            posts: db.FieldValue.arrayUnion(doc.id),
          });
      });
    });
  }
  return (
    <>
      {/* <MainHeader /> */}
      <ContentBox>
        <form onSubmit={handleUpload}>
          <Info>
            <img src="img/coro.png" alt="ProfileImage" className="profileImg" />
            <Title>
              장소 이름 &nbsp;
              <input
                className="title"
                name="title"
                value={title}
                onChange={handleChange}
                required
              />
              <br />
              카테고리 &nbsp;
              <select
                className="category"
                name="category"
                onChange={handleChange}
                value={category}
              >
                {categoryList.map((item) => (
                  <option value={item} key={item}>
                    {item}
                  </option>
                ))}
              </select>
            </Title>
          </Info>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <div style={{ width: "450px" }}>
              <ImgBox image={imgBase64}></ImgBox>
              <input
                type="file"
                name="imgFile"
                id="imgFile"
                onChange={handleChange}
              />
            </div>
            <TextInput
              name="text"
              placeholder="글 내용"
              value={text}
              onChange={handleChange}
            />
          </div>
          <BtnBox>
            <button disabled={!file || text === ""}>업로드</button>
          </BtnBox>
        </form>
      </ContentBox>
    </>
  );
}

export default CreatePost;
