import React, { useEffect, useState } from "react";
import styled, { css } from "styled-components";
import { BiLike } from "react-icons/bi";
import { auth, db } from "../firebase";
import { useHistory } from "react-router-dom";

const Box = styled.div`
  width: 440px;
  padding: 5px;
  border-radius: 15px;

  &:hover {
    background-color: #e9ecef;
  }
`;
const Info = styled.div`
  display: flex;
  margin-bottom: 10px;

  & .profileImg {
    width: 50px;
    border-radius: 50%;
  }
`;
const Title = styled.div`
  margin-left: 10px;

  & .title {
    font-weight: bold;
    font-size: 20px;
  }
  & .category {
    font-size: 15px;
  }
`;
const ImgBox = styled.div`
  border: 1px solid #495057;
  background-color: #dee2e6;
  border-radius: 10px;
  height: 250px;
  ${(props) =>
    props.image &&
    css`
      background-image: url(${props.image});
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
    `}
`;
const TextBox = styled.div`
  margin: 5px 0;

  & textarea {
    width: 100%;
    height: 45px;
    max-height: 70px;
    min-height: 20px;
    max-width: 100%;
    min-width: 100%;
    border: none;
    background-color: inherit;
  }
`;
const BtnBox = styled.div`
  display: flex;
  justify-content: space-between;

  & .like-btn {
    width: 50px;
    height: 20px;
    border: 1px solid black;
    border-radius: 10px;
    margin-right: 10px;
    display: flex;
    align-items: center;
    padding: 0 5px;
    cursor: pointer;
  }
  ${(props) =>
    props.check &&
    css`
      & .like-btn {
        background: #5292f3d0;
        color: white;
      }
    `}

  & .lookPost {
    display: flex;

    & p {
      cursor: pointer;
      user-select: none;
    }
  }
`;

function PostItem({
  id,
  profileImgUrl,
  title,
  category,
  image,
  text,
  likeNum,
}) {
  const history = useHistory();
  const [user, setUser] = useState(null);
  const [likeCheck, setLikeCheck] = useState(false);
  useEffect(() => {
    setUser(auth().currentUser);
  }, []);
  useEffect(() => {
    if (user != null) {
      db()
        .collection("like")
        .doc(user.uid)
        .get()
        .then((doc) => {
          try {
            setLikeCheck(doc.data()[id].like);
          } catch {
            db()
              .collection("like")
              .doc(user.uid)
              .set({
                [id]: { like: false },
              });
          }
        });
    }
  }, [id, user]);

  const [likeCnt, setLikeCnt] = useState(likeNum);

  const onLikeClick = () => {
    if (user === null) {
      alert("로그인 후 이용할 수 있습니다.");
    } else {
      setLikeCheck(!likeCheck);
      if (likeCheck) {
        setLikeCnt((prevNum) => prevNum - 1);
        db()
          .collection("like")
          .doc(user.uid)
          .update({
            [id]: { like: false },
          });
        db()
          .collection("post")
          .doc(id)
          .update({
            likeNum: likeCnt - 1,
          });
      } else {
        setLikeCnt((prevNum) => prevNum + 1);
        db()
          .collection("like")
          .doc(user.uid)
          .update({
            [id]: { like: true },
          });
        db()
          .collection("post")
          .doc(id)
          .update({
            likeNum: likeCnt + 1,
          });
      }
    }
  };

  const onLookPost = () => {
    history.push({
      pathname: "/postpage",
      state: { type: "postId", postId: id, likeNum: likeCnt },
    });
  };

  return (
    <Box>
      <Info>
        <img src={profileImgUrl} alt="ProfileImage" className="profileImg" />
        <Title>
          <p className="title">{title}</p>
          <p className="category">{category}</p>
        </Title>
      </Info>
      <ImgBox image={image}></ImgBox>
      <TextBox>
        {/* <p>{text}</p> */}
        <textarea value={text} readOnly />
      </TextBox>
      <BtnBox check={likeCheck}>
        <div className="like-btn" onClick={onLikeClick}>
          <BiLike />
          {likeCnt}
        </div>
        <div className="lookPost" onClick={onLookPost}>
          <p>자세히보기</p>
        </div>
      </BtnBox>
    </Box>
  );
}

export default PostItem;
