import React, { useEffect, useState } from "react";
import styled from "styled-components";
import "../css/MainHeader.css";
import { Link, useHistory } from "react-router-dom";
import { logout } from "../auth";
import { auth } from "../firebase";

const Category = styled.div`
  display: flex;
  & p {
    padding: 5px;
    margin: 5px;
    cursor: pointer;
  }
  & p:hover {
    background-color: #5292f3d0;
    border-radius: 30px;
    color: white;
  }
`;
const Header = styled.div`
  max-width: 1000px;
  margin: 0 auto;
  padding-top: 10px;
`;

const SearchInput = styled.input`
  border: 1px solid black;
  border-radius: 15px;
  padding: 5px 10px;
  outline: none;
`;

function MainHeader() {
  const history = useHistory();
  const [authenticated, setAuthenticated] = useState(false);
  useEffect(() => {
    auth().onAuthStateChanged((user) => {
      if (user) {
        setAuthenticated(true);
      } else {
        setAuthenticated(false);
      }
    });
  }, []);

  const [isCategory, setIsCategory] = useState(false);

  const onLogout = () => {
    logout();
    window.location.reload();
  };

  const [searchInput, setSearchInput] = useState("");
  const onChange = (e) => {
    setSearchInput(e.target.value);
  };
  const onSearch = (e) => {
    e.preventDefault();
    history.push({
      pathname: "/search",
      state: { type: "search", userInput: searchInput },
    });
  };

  const onCategory = (e) => {
    history.push({
      pathname: "/search",
      state: { type: "category", userInput: e.target.id },
    });
  };

  return (
    <>
      <Header>
        <div className="main">
          <Link to="/">
            <img src="img/title-logo.png" alt="LOGO" className="logo-img" />
          </Link>
          <form onSubmit={onSearch}>
            <SearchInput
              type="search"
              className="search"
              value={searchInput}
              onChange={onChange}
              placeholder="검색"
            />
          </form>
          {!authenticated && (
            <div className="login">
              <Link to="/login" className="login-btn">
                <p>로그인</p>
              </Link>
              <Link to="join">
                <div className="signup-btn">
                  <p>회원가입</p>
                </div>
              </Link>
            </div>
          )}
          {authenticated && (
            <div className="login">
              <Link to="/mypage" className="login-btn">
                <p>마이페이지</p>
              </Link>
              <div className="signup-btn" onClick={onLogout}>
                <p>로그아웃</p>
              </div>
            </div>
          )}
        </div>
        <div className="menu">
          <div
            className="upload-btn"
            onMouseOver={() => {
              setIsCategory(true);
            }}
            onMouseOut={() => {
              setIsCategory(false);
            }}
            style={
              isCategory
                ? { boxShadow: "inset 0 -3px 0 0 #5292f3d0" }
                : { boxShadow: "none" }
            }
          >
            카테고리
          </div>
          {authenticated && (
            <Link to="/post">
              <div className="upload-btn">글쓰기</div>
            </Link>
          )}
        </div>
        <Category
          style={isCategory ? { display: "flex" } : { display: "none" }}
          onMouseOver={() => {
            setIsCategory(true);
          }}
          onMouseOut={() => {
            setIsCategory(false);
          }}
        >
          <p onClick={onCategory} id="아시아">
            아시아
          </p>
          <p onClick={onCategory} id="아프리카">
            아프리카
          </p>
          <p onClick={onCategory} id="유럽">
            유럽
          </p>
          <p onClick={onCategory} id="아메리카">
            아메리카
          </p>
          <p onClick={onCategory} id="오세아니아">
            오세아니아
          </p>
        </Category>
      </Header>
      <hr />
    </>
  );
}

export default MainHeader;
